
  .nav {
    display: flex;
    justify-content:space-evenly;
    align-items: center;
    background-color: white;
    height: 100px;
    border-bottom: 2px solid black;
  }
  
  .navBrandDiv {
    display: flex;
    align-items: center;
  }
  
  .navBrand {
    font-weight: bold;
    font-size: 28px;
    text-decoration: none;
    margin-left: 10px;
    color: black;
  }
  
  .navList {
    display: flex;
    list-style: none;
    /* margin: 0; */
    padding: 0;
  }
  
  .navItem {
    margin: 0 10px;
  }
  
  .navItem a {
    color: black;
    text-decoration: none;
    font-size: 20px;
  }
  
  @media screen and (max-width: 768px) {
    .navbar-header {
      justify-content: space-between;
      width: 100%;
      padding: 0 10px;
    }
    
    .navbar-toggle {
      display: block;
    }
    
    .navbar-nav {
      display: none;
      flex-direction: column;
      background-color: #333;
      position: absolute;
      top: 100%;
      left: 0;
      right: 0;
      padding: 10px;
    }

  }